<template>
    <div class="modal" @click="$emit('closemodal')">
        <div class="modal__content" @click.stop>
            <h3 class="modal__content-title">{{ id ? 'Edit' : 'Add' }} note</h3>
            <form action="" class="modal__content-form" @submit.prevent="$emit('addOrChange', id)">
                <input type="title" class="modal__content-inp" @input="$emit('inpVal', $event.target.value)" :value="title" placeholder="Title">
                <textarea name="aa" id="" cols="30" rows="6" class="modal__content-area" required placeholder="Content"
                    @input="$emit('areaVal', $event.target.value)" :value="body"></textarea>
                <div class="modal__content-btns">
                    <span class="modal__content-cancel" @click="$emit('closemodal')">Cancel</span>
                    <button class="modal__content-add">{{ id ? 'Edit': 'add'}}</button>
                </div>
            </form>
        </div>
    </div>
</template>                                                                                                                                                                                                    
<script>
export default {
    name: "modal",
    props: {
        title: {
            type: String,
            default: "",
        },
        body: {
            type: String,
            default: "",
        },
        id:{
            type: Number,
            default: null
        }
    }
}                                                                                                     
</script>                                                                                                     
                                                                                                     
<style lang="scss" scoped></style>                                                                                                     