<template>
    <div class="notes__card">
        <h3 class="notes__card-title">{{ note.title }}</h3>
        <span class="notes__card-time">{{ note.time}}</span>
        <p class="notes__card-txt">{{ note.body }}</p>
        <div class="notes__card-control">
            <span class="notes__card-edit" @click="$emit('editTask', note.id)">
                <img src="../../img/icons/edit.svg" alt="" class="notes__card-icon">
                Edit
            </span>
            <span class="notes__card-del" @click="$emit('delTask', note.id)" >
                <img src="../../img/icons/delete.svg" alt="" class="notes__card-icon">
                Delete
            </span>
        </div>
    </div>
</template>
<script>
export default {
    name: "NotesItem",
    props:{
        note:{
            type: Object,
            required: true
        }
        
    }
    
}
</script>

