<template>
    <header class="header">
        <div class="container">
            <h1 class="header__title">Notes</h1>
            <form action="" class="header__form">
                <input type="text" class="header__form-input" placeholder="Search..." v-model="search">
            </form>
        </div>
    </header>
    
    
</template>

<script>

export default{
    name : "Header",
    data(){
        return{
            search: ""
        }
    },
    watch:{
        search(newVal, oldVal){
            this.$emit("search",newVal)
        }
    }
}



</script>


<style>

.header{
    background: var(--bg);
}

</style>