<template>
  <div class="wapper">
    <Header @search="search = $event"/>
    <main>
      <Notes :notes="filterNotes" @delTask="delTask" @editTask="editTask"/>
    </main>
    <Teleport to="#body">
    <transition name="modal">
      <Modal v-show="isShowModal" @closemodal="isShowModal = false" @inpVal="modalTitle = $event"
        @areaVal="modalBody = $event" @addOrChange="addOrChange" :title="modalTitle" :body="modalBody" :id="curID" />
        
    </transition>
    </Teleport>
    <span class="open-modal" @click="isShowModal = true, modalTitle='', modalBody= '', curID = null ">
      <img src="./img/icons/add-note.svg" alt="">
    </span>
  </div>
</template>

<script>

import Header from "./components/common/header.vue";
import Notes from "./components/notes/notes.vue";
import Modal from "./components/UI/modal.vue";

export default {
  name: 'App',
  components: {
    Header,
    Notes,
    Modal
  },
  data() {
    return {
      notes: [
        {
          id: 1,
          title: "Task 1",
          body: "Lorem ipsum dolor sit, amet consectetur adipisicing elit. Odit voluptas sit aliquam ea accusamus, obcaecati velit cum mollitia nihil blanditiis eaque officiis provident nisi qui corporis dolorum, commodi quis nulla.",
          time: "15.07.2023"
        },
        {
          id: 2,
          title: "Task 2",
          body: "Lorem ipsum dolor sit, amet consectetur adipisicing elit. Odit voluptas sit aliquam ea accusamus, obcaecati velit cum mollitia nihil blanditiis eaque officiis provident nisi qui corporis dolorum, commodi quis nulla.",
          time: "25.03.2023"
        },
        {
          id: 3,
          title: "Task 3",
          body: "Lorem ipsum dolor sit, amet consectetur adipisicing elit. Odit voluptas sit aliquam ea accusamus, obcaecati velit cum mollitia nihil blanditiis eaque officiis provident nisi qui corporis dolorum, commodi quis nulla.",
          time: "05.09.2023"
        },
      ],
      isShowModal: false,
      modalTitle: "",
      modalBody: "",
      curID: null,
      search: ""
    }
  },
  methods: {
    addOrChange(id) {
      let obj = {
        id: new Date().getTime(),
        title: this.modalTitle,
        body: this.modalBody,
        time: new Date(Date.now()).toLocaleDateString()
      }
      if(id ){
        let idx = this.notes.findIndex((item) => item.id == id)
        this.notes[idx] = obj
      }else{
        this.notes.push(obj)
      }
        this.modalTitle = "",
        this.modalBody = ""
      this.isShowModal = false
    },
    delTask(id) {
      let idx = this.notes.findIndex((item) => item.id == id)
      this.notes.splice(idx, 1)
    },
    editTask(id){
      this.isShowModal = true
      let editID = this.notes.find(item => item.id == id)
      this.modalTitle = editID.title 
      this.modalBody = editID.body
      this.curID = editID.id
    
  },
  },
  computed:{
    filterNotes(){
      return this.search ? this.notes.filter(item => item.body.toLowerCase().includes(this.search.toLowerCase())) : this.notes
    }
  }
}
</script>